import {
	getUserAuth,
	auth2Page
} from '../../../utils/login.js'
import {
	showPic,
	toast,
	navTo
} from '../../../utils/default.js'
import {
	cloudFn
} from '../../../lib/cloudFn.js'
export default {
	data() {
		return {
			userAuth: false,
			userInfo: null,
			exercise: {
				clock: null,
				clockState: false,
				start: '',
				end: '',
				duration: '00:00:00',
			}
		}
	},
	methods: {
		showPic,
		navTo,
		getUserAuth(e) {
			if (getUserAuth.call(this, e)) {
				this.userAuth = true;
				this.userInfo = getApp().globalData.userInfo;
			}
		},
		clockChange(history = false) {
			if (!history) this.exercise.clockState = !this.exercise.clockState;
			if (this.exercise.clockState) {
				console.log(this.exercise);
				this.exercise.end = '';
				this.exercise.clockState = true;
				this.exercise.uploaded = false;
				let tick = new Date(0, 0, 0, 0, 0, 1);
				if (history) {
					tick.setSeconds((new Date().getTime() - new Date(this.exercise.start).getTime()) / 1000)
				} else {
					this.exercise.start = new Date().toLocaleString('chinese', {
						hour12: false
					});
				}
				this.exercise.duration = tick.toLocaleString('chinese', {
					hour12: false
				})
				this.exercise.clock = setInterval(() => {
					tick.setSeconds(tick.getSeconds() + 1);
					// console.log(1);
					this.exercise.duration = tick.toLocaleString('chinese', {
						hour12: false
					});
					if (tick.getHours() > 23) {
						this.clockChange();
					}
				}, 1000)
			} else {
				if (!history) {
					this.exercise.end = new Date().toLocaleString('chinese', {
						hour12: false
					});
				}
				this.exercise.clockState = false;
				clearInterval(this.exercise.clock);
				this.updateLog();
			}
		},
		getLogs() {
			cloudFn({
					name: 'getLogs',
					data: {
						size: 1,
						current: 1,
					}
				})
				.then(r => {})
		},
		updateLog() {
			if (this.exercise.uploaded) return;
			let duration = new Date(this.exercise.end).getTime() - new Date(this.exercise.start).getTime();
			if (duration < 5 * 60 * 1000) {
				toast({
					title: '练习时间少于5分钟不计入哦，请加油~',
					duration: 3000
				})
				return
			}
			cloudFn({
					name: 'uploadLog',
					data: {
						start: this.exercise.start,
						end: this.exercise.end,
						duration,
					}
				})
				.then(r => {
					this.exercise.uploaded = true;
					uni.setStorage({
						key: 'exercise',
						data: this.exercise
					})
				})
		}
	},
	onLoad() {
		console.log('Mine onLoad');
		this.$state.setWatch(this, 'userAuth', 'userAuth');
		this.userInfo = getApp().globalData.userInfo;
		getApp().loginCB = () => {
			console.log('loginCB');
			this.userInfo = getApp().globalData.userInfo;
		}
	},
	onShow() {
		let history = uni.getStorageSync('exercise')
		if (history) {
			this.exercise = history;
			this.clockChange(true);
		}
	},
	onHide() {
		clearInterval(this.exercise.clock)
		uni.setStorage({
			key: 'exercise',
			data: this.exercise
		})
	},
	onUnload() {
		clearInterval(this.exercise.clock)
		uni.setStorage({
			key: 'exercise',
			data: this.exercise
		})
	}
}
